body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #553055;
}

/* app styles */
.App {
  max-width: 700px;
  color: #eeeeee;
  width: 90%;
  margin: 30px auto;
  background: #4c2a4c;
}

.navbar {
  padding: 10px 20px;
  text-align: center;
  background: #6d3d6d;
}
navbar h1 {
  margin: 10px 0;
}
